import React, { Component } from 'react';
import { TYPES } from '../helpers/constants';
import { hexToRgba } from '../helpers';
import './index.css';

export default class Ability extends Component {
    constructor(props) {
        super(props);

        this.state = {display: false}
    }

    handleClick = () => {
        this.setState({
            display: !this.state.display
        });
    }

    render() {
        const props = this.props;
        const abilities = props.abilities;
        const display = this.state.display;
        const moves = props.moves;
        const isAbility = abilities;
        const buttonClassNames = `ability_button ${display && 'display'}`;
        let button = '';

        if(isAbility) {
            const name = abilities[0];
            const description = abilities[1];

            button = (
                <div className="ability">
                    <button className={buttonClassNames} onClick={this.handleClick}>
                        {name}
                    </button>
                    {
                        display && <div className="ability_description">
                            {description}
                        </div>
                    }
                </div>
            );
        } else {
            const name = moves[0];
            const type = moves[1];
            const effect = moves[2];
            const accuracy = moves[3];
            const damage = moves[4];

            button = (
                <div className="ability">
                    <button onClick={this.handleClick} className={buttonClassNames} style={{backgroundColor: `${hexToRgba(TYPES[type], 0.5)}`}}>
                        { (accuracy || damage) && (
                            <span className="ability_stats">
                                {
                                    accuracy && <span className="ability_stats-acc" title="Accuracy">
                                        {accuracy}
                                    </span>
                                }
                                {
                                    damage && <span className="ability_stats-dmg" title="Damage">
                                        {damage}
                                    </span>
                                }
                            </span>)
                        }
                        <span className="ability_name">
                            {name}
                        </span>
                        <span className="ability_type" style={{backgroundColor: `#${TYPES[type]}`}}>
                            {type}
                        </span>
                    </button>
                    {
                        display && <div className="ability_description">
                            {effect}
                        </div>
                    }
                </div>
            );
        }

        return (
            <li className="ability">
                { button }
            </li>
        );
    }
}