import React, { Component } from 'react';
import { fetchData, actionNames, stats} from '../../state/actions';
import './index.css';
import { TYPENAMES, TYPES} from '../helpers/constants';

export default class Menu extends Component {
    constructor(props) {
        super(props);
        this.state = {
            speed: 0,
            sDef: 0,
            sAtt: 0,
            def: 0,
            att: 0,
            hp: 0,
            currentOrder: ''
        };
    }
    handleReset = () => {
        this.props.dispatch({
            type: actionNames.CURRENT_TYPE
        });
        this.props.dispatch({
            type: actionNames.NOSTAT
        });
        this.props.dispatch(fetchData());
        this.setState({
            speed: 0,
            sDef: 0,
            sAtt: 0,
            def: 0,
            att: 0,
            hp: 0,
            currentOrder: ''
        });
    };

    sortBySpeed = () => {
        let newState = this.state.speed + 1;
        let action;
        let currentType = this.props.currentType;

        
        if(newState > 2) {
            newState = 0;
        }

        switch(newState){
            case 1:
                action = { type: actionNames.SORT_BY_SPEED_ASC };
                break;
            case 2:
                action = { type: actionNames.SORT_BY_SPEED_DES };
                break;
            default:
                action = 'reset';
            break;
        }

        if(action === 'reset') {
            this.props.dispatch({
                type: actionNames.NOSTAT
            });
            this.props.dispatch(fetchData(currentType));
        } else {
            this.props.dispatch(action);
            this.props.dispatch({
                type: stats.SPEED
            });
        }

        this.setState({
            speed: newState,
            sDef: 0,
            sAtt: 0,
            def: 0,
            att: 0,
            hp: 0,
            currentOrder: action && action.type
        });
    }

    sortBySDef = () => {
        let newState = this.state.sDef + 1;
        let action;
        let currentType = this.props.currentType;
        
        if(newState > 2) {
            newState = 0;
        }

        switch(newState){
            case 1:
                action = { type: actionNames.SORT_BY_SPECIAL_DEF_ASC };
                break;
            case 2:
                action = { type: actionNames.SORT_BY_SPECIAL_DEF_DES };
                break;
            default:
                action = 'reset';
            break;
        }

        if(action === 'reset') {
            this.props.dispatch({
                type: actionNames.NOSTAT
            });
            this.props.dispatch(fetchData(currentType));
        } else {
            this.props.dispatch(action);
            this.props.dispatch({
                type: stats.SDEF
            });
        }

        this.setState({
            speed: 0,
            sDef: newState,
            sAtt: 0,
            def: 0,
            att: 0,
            hp: 0,
            currentOrder: action && action.type
        });
    }

    sortBySAtt = () => {
        let newState = this.state.sAtt + 1;
        let action;
        let currentType = this.props.currentType;

        
        if(newState > 2) {
            newState = 0;
        }

        switch(newState){
            case 1:
                action = { type: actionNames.SORT_BY_SPECIAL_ATT_ASC };
                break;
            case 2:
                action = { type: actionNames.SORT_BY_SPECIAL_ATT_DES };
                break;
            default:
                action = 'reset';
            break;
        }

        if(action === 'reset') {
            this.props.dispatch({
                type: actionNames.NOSTAT
            });
            this.props.dispatch(fetchData(currentType));
        } else {
            this.props.dispatch(action);
            this.props.dispatch({
                type: stats.SATT
            });
        }

        this.setState({
            speed: 0,
            sDef: 0,
            sAtt: newState,
            def: 0,
            att: 0,
            hp: 0,
            currentOrder: action && action.type
        });
    }

    sortByDef = () => {
        let newState = this.state.def + 1;
        let action;
        let currentType = this.props.currentType;

        
        if(newState > 2) {
            newState = 0;
        }

        switch(newState){
            case 1:
                action = { type: actionNames.SORT_BY_DEFENSE_ASC };
                break;
            case 2:
                action = { type: actionNames.SORT_BY_DEFENSE_DES };
                break;
            default:
                action = 'reset';
            break;
        }

        if(action === 'reset') {
            this.props.dispatch({
                type: actionNames.NOSTAT
            });
            this.props.dispatch(fetchData(currentType));
        } else {
            this.props.dispatch(action);
            this.props.dispatch({
                type: stats.DEF
            });
        }

        this.setState({
            speed: 0,
            sDef: 0,
            sAtt: 0,
            def: newState,
            att: 0,
            hp: 0,
            currentOrder: action && action.type
        });
    }
    
    sortByAtt = () => {
        let newState = this.state.att + 1;
        let action;
        let currentType = this.props.currentType;

        
        if(newState > 2) {
            newState = 0;
        }

        switch(newState){
            case 1:
                action = { type: actionNames.SORT_BY_ATTACK_ASC };
                break;
            case 2:
                action = { type: actionNames.SORT_BY_ATTACK_DES };
                break;
            default:
                action = 'reset';
            break;
        }

        if(action === 'reset') {
            this.props.dispatch({
                type: actionNames.NOSTAT
            });
            this.props.dispatch(fetchData(currentType));
        } else {
            this.props.dispatch(action);
            this.props.dispatch({
                type: stats.ATT
            });
        }

        this.setState({
            speed: 0,
            sDef: 0,
            sAtt: 0,
            def: 0,
            att: newState,
            hp: 0,
            currentOrder: action && action.type
        });
    }

    sortByHP = () => {
        let newState = this.state.hp + 1;
        let action;
        let currentType = this.props.currentType;
        
        if(newState > 2) {
            newState = 0;
        }

        switch(newState){
            case 1:
                action = { type: actionNames.SORT_BY_HIT_POINTS_ASC };
                break;
            case 2:
                action = { type: actionNames.SORT_BY_HIT_POINTS_DES };
                break;
            default:
                action = 'reset';
            break;
        }

        if(action === 'reset') {
            this.props.dispatch({
                type: actionNames.NOSTAT
            });
            this.props.dispatch(fetchData(currentType));
        } else {
            this.props.dispatch(action);
            this.props.dispatch({
                type: stats.HP
            });
        }

        this.setState({
            speed: 0,
            sDef: 0,
            sAtt: 0,
            def: 0,
            att: 0,
            hp: newState,
            currentOrder: action && action.type
        });
    }

    toggleTypes = () => {
        this.props.dispatch({
            type: actionNames.TOGGLE_TYPES
        });
    }

    getClassName = (filter) => {
        let dir = this.state[filter] || 0;
        let dirClass;

        switch (dir) {
            case 1:
                dirClass = 'asc';
                break;
            case 2:
                dirClass = 'des';
                break;
            default:
                dirClass = '';
                break;
        }

        return `menu-list-button ${filter} ${dirClass}`;
    }

    handleType = (e) => {
        const type = e.target.innerText.toLowerCase();
        this.props.dispatch({
            type: actionNames.TOGGLE_TYPES
        });
        this.props.dispatch(fetchData(type, this.state.currentOrder));
    }

    getButtons = () => {
        return TYPENAMES.map((type) => {
            let style = {
                backgroundColor: `#${TYPES[type]}`
            };
            
            return (
                <button 
                    key={type}
                    onClick={this.handleType}
                    className='menu_types_list'
                    style={style}
                >
                    {type}
                </button>
            )
        })
    }

    render() {
        const toggleTypes = this.props.toggleTypes;
        const currentType = this.props.currentType;
        const typeClass   = currentType ? 'type-selected' : this.getClassName('type');
        const style = {
            backgroundColor: currentType && (typeClass == 'type-selected') && `#${TYPES[currentType]}` || ''
        };

        return (
            <nav className="menu">
                <ul className="menu-list">
                    <li className="menu-list-item">
                        <button className={this.getClassName('clear')} onClick={this.handleReset}>Clear</button>
                    </li>
                    <li className="menu-list-item">
                        <button className={typeClass} style={style} onClick={this.toggleTypes}>{currentType || 'Type'}</button>
                    </li>
                    <li className="menu-list-item">
                        <button className={this.getClassName('speed')} onClick={this.sortBySpeed}>Speed</button>
                    </li>
                    <li className="menu-list-item">
                        <button className={this.getClassName('sDef')} onClick={this.sortBySDef}>special Defense</button>
                    </li>
                    <li className="menu-list-item">
                        <button className={this.getClassName('sAtt')} onClick={this.sortBySAtt}>special Attack</button>
                    </li>
                    <li className="menu-list-item">
                        <button className={this.getClassName('def')} onClick={this.sortByDef}>Defense</button>
                    </li>
                    <li className="menu-list-item">
                        <button className={this.getClassName('att')} onClick={this.sortByAtt}>Attack</button>
                    </li>
                    <li className="menu-list-item">
                        <button className={this.getClassName('hp')} onClick={this.sortByHP}>Hit Points</button>
                    </li>
                </ul>
                {
                    toggleTypes && (
                        <div className="menu-types">
                            {this.getButtons()}
                        </div>
                    )
                }
            </nav>
        );
    }
}