import React, { Component } from 'react';
import './index.css';
import Ability from '../Ability';

export default class Abilities extends Component {
    constructor(props) {
        super(props);
        this.state = {
            abilities: {},
            moves: {}
        }
    }

    getButton = (item) => {
        const state = this.state;
        const abilities = state.abilities && state.abilities[item-1];
        const moves = state.moves && state.moves[item-1];
        const key = (moves && moves[0]) || (abilities && abilities[0]);

        return (
            <Ability
                abilities={abilities}
                moves={moves}
                key={key}
            />
        );
    }

    componentDidMount() {
        const props = this.props;
        const isAbility = props.isAbility;

        if(isAbility){
            fetch('./abilities_min.json')
            .then((response)=>{
                return response.json();
            })
            .then((data)=>{
                this.setState({
                    abilities: data
                })
            })
            .catch(
                this.setState({
                    abilities: {}
                })
            );
        } else {
            fetch('./moves_min.json')
            .then((response)=>{
                return response.json();
            })
            .then((data)=>{
                this.setState({
                    moves: data
                })
            })
            .catch(
                this.setState({
                    moves: {}
                })
            )
        }
    }

    render() {
        const props = this.props;
        const list  = props.list || [];

        const items = list.map((item)=>(
                this.getButton(item)
            )
        );

        return (
            <ul className="lightBox_abilities">
                {items}
            </ul>
        )
    }
}