export const stats = {
    SPEED : "SPEED",
    SDEF  : "SDEF",
    SATT  : "SATT",
    DEF   : "DEF",
    ATT   : "ATT",
    HP    : "HP",
    NOSTAT: "NOSTAT"
}

export const actionNames = {
  FETCH_POKEDEX_SUCCEDED  : "FETCH_POKEDEX_SUCCEDED",
  FETCH_POKEDEX_FAILED    : "FETCH_POKEDEX_FAILED",
  FETCH_LOADING           : "FETCH_LOADING",
  SORT_BY_SPEED_ASC       : "SORT_BY_SPEED_ASC",
  SORT_BY_SPECIAL_DEF_ASC : "SORT_BY_SPECIAL_DEF_ASC",
  SORT_BY_SPECIAL_ATT_ASC : "SORT_BY_SPECIAL_ATT_ASC",
  SORT_BY_DEFENSE_ASC     : "SORT_BY_DEFENSE_ASC",
  SORT_BY_ATTACK_ASC      : "SORT_BY_ATTACK_ASC",
  SORT_BY_HIT_POINTS_ASC  : "SORT_BY_HIT_POINTS_ASC",
  SORT_BY_SPEED_DES       : "SORT_BY_SPEED_DES",
  SORT_BY_SPECIAL_DEF_DES : "SORT_BY_SPECIAL_DEF_DES",
  SORT_BY_SPECIAL_ATT_DES : "SORT_BY_SPECIAL_ATT_DES",
  SORT_BY_DEFENSE_DES     : "SORT_BY_DEFENSE_DES",
  SORT_BY_ATTACK_DES      : "SORT_BY_ATTACK_DES",
  SORT_BY_HIT_POINTS_DES  : "SORT_BY_HIT_POINTS_DES",
  TOGGLE                  : "TOGGLE",
  TOGGLE_TYPES            : "TOGGLE_TYPES",
  SPEED                   : stats.SPEED,
  SDEF                    : stats.SDEF,
  SATT                    : stats.SATT,
  DEF                     : stats.DEF,
  ATT                     : stats.ATT,
  HP                      : stats.HP,
  NOSTAT                  : stats.NOSTAT,
  CURRENT_ID              : "CURRENT_ID",
  CURRENT_TYPE            : "CURRENT_TYPE",
  FILTER_BY_TYPE          : "FILTER_BY_TYPE"
};

export function fetchData(type, currentOrder) {
    return (dispatch, getState) => {
        const ENDPOINT = './pokedex_min.json';

        dispatch({
            type: actionNames.FETCH_LOADING
        });

        return fetch(ENDPOINT)
            .then(res => res.json())
            .then(pokemons => {
                if(type && type !== 'all'){
                    dispatch({
                        type: actionNames.FETCH_POKEDEX_SUCCEDED,
                        payload: pokemons.slice().filter((item)=>(item[3].indexOf(type)>-1))
                    });
                } else {
                    dispatch({
                        type: actionNames.FETCH_POKEDEX_SUCCEDED,
                        payload: pokemons
                    });
                }
            })
            .then(res => {
                if(type && type !== 'all'){
                    dispatch({
                        type: actionNames.CURRENT_TYPE,
                        payload: type
                    })
                }
            })
            .then(res => {
                if(currentOrder){
                    dispatch({
                        type: currentOrder
                    });
                }
            })
            .catch(error => {
                dispatch({
                    type: actionNames.FETCH_POKEDEX_FAILED,
                    payload: error
                })
            });
    }
}

export function setType(type) {
    return (dispatch) => {
        let typePromise = new Promise((resolve, reject)=>resolve());

        return typePromise.then(()=>(
            dispatch({
                type: actionNames.CURRENT_TYPE,
                payload: type
            })
        ));
    }
}