import React, { Component , Fragment } from 'react';
import './index.css';
import { TYPES, ACTIONS } from '../helpers/constants';
import { hexToRgba } from '../helpers';
import Abilities from '../Abilities';

export default class LightBox extends Component {
    handleClick = () => {
        const props = this.props;
        const dispatch = props.dispatch;

        if(typeof dispatch === 'function'){
            dispatch({
                type: ACTIONS.TOGGLE
            });
        }
    }

    handleSlide = (e) => {
        const props = this.props;
        const id = props.id;
        const data = props.data || [];
        const currId = data.length && data.findIndex((item)=>item[0] === id);
        const totalItems = props.totalItems - 1;
        const dispatch = props.dispatch;
        const classNameList = e.target.classList;
        const newId = classNameList.contains('prev') ? (currId - 1) : (currId + 1);
        let fallBackId = (newId >= 1) ? newId : (totalItems);

        fallBackId = (fallBackId <= totalItems) ? fallBackId : 1;

        if(typeof dispatch === 'function'){
            dispatch({
                type: ACTIONS.CURRENT_ID,
                payload: data[fallBackId][0]
            });
        }
    }

    handleButtonClick = (e) => {
        e.stopPropagation();
    }

    getGradient = (colora, colorb) => {
        return `linear-gradient(to bottom, ${colora} 0%,${colorb || colora} 100%)`
    }

    getHeader = () => {
        const props = this.props;
        const data = props.data || [];
        const id = props.id;
        const currId = data.length && data.findIndex((item)=>item[0] === id);
        const currentData = data[currId] || [];
        const name = currentData[1];
        const type = currentData[3] || [];
        let styleNames;

        if(type.length) {
            styleNames = {
                color: `${hexToRgba(TYPES[type[0]], 0.3, 20)}`
            };
        }

        return (
            <Fragment>
                    <div className='lightBox_imageContainer_name1' style={styleNames}>{name}</div>
                    <div className='lightBox_imageContainer_name2' style={styleNames}>{name}</div>
                    <div className='lightBox_imageContainer_name3' style={styleNames}>{name}</div>
                    <div className='lightBox_imageContainer_name4' style={styleNames}>{name}</div>
                    <div className='lightBox_imageContainer'>
                        <img className='lightBox_image' alt={name} src={`./imagehd/${id}.png`}/>
                    </div>
                    <h3 className="lightBox_name">{`#${id} ${name}`}</h3>
            </Fragment>
        );
    }

    render() {
        const props = this.props;
        const display = props && props.display;
        const displayClass = display ? 'show': '';
        const classNames = `lightBox ${displayClass}`;
        const data = props.data || [];
        const id = props.id;
        const currId = data.length && data.findIndex((item)=>item[0] === id);
        const currentData = data[currId] || [];
        const type = currentData[3] || [];
        const moves = currentData[4] || [];
        const abilities = currentData[5] || [];
        const types = type.map((item, index)=><li className='lightBox_types_list' key={item} style={{backgroundColor: `#${TYPES[type[index]]}`}}>{item}</li>);
        const colorBgA = type.length && hexToRgba(TYPES[type[0]], 0.5);
        const colorBgB = type.length && type[1] && hexToRgba(TYPES[type[1]], 0.5);
        const colorBoxA = type.length && hexToRgba(TYPES[type[0]], 1);
        const colorBoxB = type.length && type[1] && hexToRgba(TYPES[type[1]], 1);
        const gradientBg = this.getGradient(colorBgA, colorBgB);
        const gradientBox = this.getGradient(colorBoxA, colorBoxB);
        const stats = (currentData && currentData[6]) || [];
        const speed = stats[0];
        const specialDefense = stats[1];
        const specialAttack = stats[2];
        const defense = stats[3];
        const attack = stats[4];
        const hp = stats[5];
        let styleBg;
        let styleBox;

        if(type.length) {
            styleBg = {
                backgroundImage: gradientBg
            };
            styleBox = {
                backgroundImage: gradientBox
            };
        }

        return (
            <div className={classNames} onClick={this.handleClick} style={styleBg}>
                <button className="lightBox_close">Close</button>
                <div className="lightBox_content" onClick={this.handleButtonClick} style={styleBox}>
                    {this.getHeader()}
                    <div className="lightBox_info">
                        <h3 className='lightBox_heading'>Types</h3>
                        <ul className='lightBox_types'>
                            {types}
                        </ul>
                        <h3 className="lightBox_heading">Stats</h3>
                        <ul className='lightBox_stats'>
                            <li title='Speed' className='lightBox_stats-item speed'>
                                {speed}
                            </li>
                            <li title='special Defense' className='lightBox_stats-item specialDefense'>
                                {specialDefense}
                            </li>
                            <li title='special Attack' className='lightBox_stats-item specialAttack'>
                                {specialAttack}
                            </li>
                            <li title='Defense' className='lightBox_stats-item defense'>
                                {defense}
                            </li>
                            <li title='Attack' className='lightBox_stats-item attack'>
                                {attack}
                            </li>
                            <li title='HP' className='lightBox_stats-item hp'>
                                {hp}
                            </li>
                        </ul>
                        <h3 className='lightBox_heading'>Special Habilities</h3>
                        <Abilities list={abilities} isAbility/>
                        <h3 className='lightBox_heading'>Moves</h3>
                        <Abilities list={moves}/>
                    </div>
                    <button className="lightBox_prev prev" onClick={this.handleSlide}>Previous</button>
                    <button className="lightBox_next next" onClick={this.handleSlide}>Next</button>
                </div>
            </div>
        );
    }
}