import React, { Component } from 'react';
import './index.css';
import { TYPES, ACTIONS } from '../helpers/constants';
import { stats as STATS } from '../../state/actions';

export default class Card extends Component {
  handleClick = () => {
    const props = this.props;
    const pokemon = (props && props.pokemon) || [];
    const dispatch = props.dispatch;
    const id = pokemon[0];

    if(dispatch && typeof dispatch === 'function'){
      dispatch({
        type: ACTIONS.CURRENT_ID,
        payload: id
      });

      dispatch({
        type: ACTIONS.TOGGLE
      });
    }
  }

  render() {
    const props = this.props;
    const pokemon = (props && props.pokemon) || [];
    const id = pokemon[0];
    const name = pokemon[1];
    const image = pokemon[2];
    const type = pokemon[3];
    const stats = pokemon[6] || [];
    const currentFilter = props && props.currFilter;
    const currentStat = (currentFilter>=0) && stats[currentFilter];
    const types = type.map((item, index)=><li className="Card_types_list" key={item} style={{backgroundColor: `#${TYPES[type[index]]}`}}>{item}</li>);
    let statClass = '';
    let style;

    switch (currentFilter) {
      case 0:
        statClass = STATS.SPEED;
        break;
      case 1:
        statClass = STATS.SDEF;
        break;
      case 2:
        statClass = STATS.SATT;
        break;
      case 3:
        statClass = STATS.DEF;
        break;
      case 4:
        statClass = STATS.ATT;
        break;
      case 5:
        statClass = STATS.HP;
        break;
      default:
        statClass = STATS.NOSTAT;
        break;
    }

    statClass = statClass.toLowerCase();
    
    if(type.length >= 2) {
      style = {
        backgroundImage: `linear-gradient(to bottom, #${TYPES[type[0]]} 0%,#${TYPES[type[1]]} 100%)`
      };
    } else if (type.length === 1) {
      style = {
        backgroundColor: `#${TYPES[type[0]]}`
      };
    }

    return (
        <button className="Card" onClick={this.handleClick} style={style}>
          <span className="Card_bg_container"><span className={'Card_bg_name'}>{name}</span></span>
          <span className="Card_id">{id}</span>
          {(currentFilter >= 0 && currentFilter != null) && <span className={`Card_currStat ${statClass}`}>{currentStat}</span>}
          <span className="Card_name">{name}</span>
          <img className="Card_image" alt={name} src={`./imagefront/${image}`}/>
          <ul className="Card_types">
            {types}
          </ul>
        </button>
    );
  }
}