import { combineReducers } from 'redux';
import { actionNames } from './actions';

const pokedexData = (state = [], action) => {
  let bodyResponse;
  
  switch(action.type) {
    case actionNames.FETCH_POKEDEX_SUCCEDED:
      bodyResponse = action.payload;
      break;
    case actionNames.FETCH_POKEDEX_FAILED:
      bodyResponse = action.payload;
      break;
    case actionNames.SORT_BY_SPEED_ASC:
      bodyResponse = state.slice().sort((a, b)=>a[6][0] - b[6][0]);
      break;
    case actionNames.SORT_BY_SPECIAL_DEF_ASC:
      bodyResponse = state.slice().sort((a, b)=>a[6][1] - b[6][1]);
      break;
    case actionNames.SORT_BY_SPECIAL_ATT_ASC:
      bodyResponse = state.slice().sort((a, b)=>a[6][2] - b[6][2]);
      break;
    case actionNames.SORT_BY_DEFENSE_ASC:
      bodyResponse = state.slice().sort((a, b)=>a[6][3] - b[6][3]);
      break;
    case actionNames.SORT_BY_ATTACK_ASC:
      bodyResponse = state.slice().sort((a, b)=>a[6][4] - b[6][4]);
      break;
    case actionNames.SORT_BY_HIT_POINTS_ASC:
      bodyResponse = state.slice().sort((a, b)=>a[6][5] - b[6][5]);
      break;
    case actionNames.SORT_BY_SPEED_DES:
      bodyResponse = state.slice().sort((a, b)=>b[6][0] - a[6][0]);
      break;
    case actionNames.SORT_BY_SPECIAL_DEF_DES:
      bodyResponse = state.slice().sort((a, b)=>b[6][1] - a[6][1]);
      break;
    case actionNames.SORT_BY_SPECIAL_ATT_DES:
      bodyResponse = state.slice().sort((a, b)=>b[6][2] - a[6][2]);
      break;
    case actionNames.SORT_BY_DEFENSE_DES:
      bodyResponse = state.slice().sort((a, b)=>b[6][3] - a[6][3]);
      break;
    case actionNames.SORT_BY_ATTACK_DES:
      bodyResponse = state.slice().sort((a, b)=>b[6][4] - a[6][4]);
      break;
    case actionNames.SORT_BY_HIT_POINTS_DES:
      bodyResponse = state.slice().sort((a, b)=>b[6][5] - a[6][5]);
      break;
    case actionNames.FILTER_BY_TYPE: 
      bodyResponse = state.slice().filter((item)=>(item[3].indexOf(action.payload)>-1));
      break;
    default:
      bodyResponse = state;
  }

  return bodyResponse;
}

const lightBox = (state = false, action) => {
  switch(action.type) {
    case actionNames.TOGGLE:
      return !state;
    default:
      return state;
  }
}

const toggleTypes = (state = false, action) => {
  switch (action.type) {
    case actionNames.TOGGLE_TYPES:
      return !state;
    default:
      return state;
  }
}

const currFilter = (state = null, action) => {
  switch (action.type) {
    case actionNames.SPEED:
      return 0;
    case actionNames.SDEF:
      return 1;
    case actionNames.SATT:
      return 2;
    case actionNames.DEF:
      return 3;
    case actionNames.ATT:
      return 4;
    case actionNames.HP:
      return 5;
    case actionNames.NOSTAT:
      return null;
    default:
      return state;
  }
}

const currType = (state = 0, action) => {
  switch (action.type) {
    case actionNames.CURRENT_TYPE:
      return action.payload || 0;
    default:
      return state;
  }
}

const currId = (state = 0, action) => {
  switch (action.type) {
    case actionNames.CURRENT_ID:
      return action.payload;
    default:
      return state;
  }
}

export default combineReducers({
  pokedexData,
  currType,
  currFilter,
  toggleTypes,
  lightBox,
  currId
});