import React, { Component } from 'react';
import { connect } from 'react-redux';
import { fetchData } from '../state/actions';
import Card from '../components/Card';
import LightBox from '../components/LightBox';
import Menu from '../components/Menu';
import './MainLayout.css';

class MainLayout extends Component {
  componentWillMount() {
    this.props.dispatch(fetchData());
  }
  
  render() {
    const props = this.props || {};
    const pokeDexData = props.state && props.state.pokedexData;
    const dispatch = props.dispatch;
    const displayBox = props.state.lightBox;
    const currId = props.state.currId;
    const toggleTypes = props.state.toggleTypes;
    const currFilter = props.state.currFilter;
    const totalItems = pokeDexData.length;
    const currType = props.state.currType;
    const pokemonCards = ((pokeDexData.length > 0) && pokeDexData.map((pokemon)=>{
    const key = pokemon.length && pokemon[1];
      return <Card pokemon={pokemon} key={key} dispatch={dispatch} currFilter={currFilter}/>
    })) || '';

    return (
      <div className="layout">
        <Menu
          dispatch={dispatch}
          toggleTypes={toggleTypes}
          currentType={currType}
        />
        <LightBox
          display={displayBox}
          dispatch={dispatch}
          data={pokeDexData}
          id={currId}
          totalItems={totalItems}
        />
        {pokemonCards}
      </div>
    );
  }
}

// Passing the ubigeos data from the global state to the container
const mapState = (state) => ({state});

// export the component connected to the global state
export default connect(mapState)(MainLayout);
